:root {
  --primary: #d81e5b;
  --secondary: #bda0bc;
  --dark: #470b7a;
  --light: #eee;
  --green: rgb(22, 254, 1);
  --red: red;
}

@font-face {
  font-family: "Digit";
  src: url("font/Matricha.ttf");  
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans', sans-serif;
}

body {
  background-color: var(--light);
}

.App {
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  padding: 16px;
}

.calculator {
  width: 100%;
  max-width: 400px;
  background-color: var(--secondary);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 50px 50px 64px rgba(0, 0, 0, 0.2);
}

.display {
  padding: 16px;
  text-align: right;
  background-color: var(--dark);
  color: var(--green);
  font-family: Digit;
  font-size: 40px;
  font-weight: 300;
  text-overflow: ellipsis;
  border-radius: 16px;
  margin: 8px;  
}

.display span {
  font-family: Digit;
  font-size: 40px;
  font-weight: 300;
  color: var(--red);
}

button {
  appearance: none;
  border: none;
  outline: none;
  color: #FFF;
  font-size: 20px;
  padding: 16px;
  cursor: pointer;
  transition: 0.4s;
}

button:hover {
  opacity: 0.5;  
}

.operators {
  display: flex;
}

#operators {
  border-radius: 16px;
  margin: 8px;
  background-color: var(--primary);
  font-weight: 700;
  letter-spacing: 0.2em;
}

.digits {
  display: flex;
  flex-wrap: wrap;
}

.digits button {
  border-radius: 16px;
  margin: 8px;
  flex: 1 1 28%;
  
  background-color: var(--dark);
}

.head {
  display: flex;
  flex-wrap: wrap;
}

.head button {  
  border-radius: 16px;
  margin: 8px;
  margin-bottom: 0px;
  cursor: none; 
  background-color: var(--dark);
}

.head button {    
  flex: 1 1 28%;  
}

.head-text  {  
  display: flex;
  flex-wrap: wrap;  
  color: var(--dark);
}

.head-text div {    
  text-align: center;
  border-radius: 16px;
  margin: 8px;
  margin-top: 0px;
  cursor: none; 
  color: var(--white);
  margin-bottom: 10px;
  flex: 1 1 28%;  
}